import Vue from 'vue'
import Router from 'vue-router'


// import UiLogin from '@/components/UIlogin'
// import mainContain from '@/components/mainContain'
// import topNav from '@/components/topNav'
// import footer from '@/components/footer'

// import mainPage1 from '@/components/mainPage1'
// import mainPage2 from '@/components/mainPage2'
// import mainPage3 from '@/components/mainPage3'

// import flex from '@/components/flex'

// import animate from '@/components/animate'
// import pubu from '@/components/pubu'
const topNav = () => import('@/components/common/topNav.vue')
const footer = () => import('@/components/common/footer.vue')
const resInfo = () => import('@/components/common/resInfo.vue')

const mainPage1 = () => import('@/components/mainPage/mainPage1.vue')
const mainPage2 = () => import('@/components/mainPage/mainPage2.vue')
const mainPage3 = () => import('@/components/mainPage/mainPage3.vue')
const mainContain = () => import('@/components/mainPage/mainContain.vue')

const mainPageDetail = () => import('@/components/mainPageDetail/mainPageDetail.vue')

const flex = () => import('@/components/flex.vue')
const animate = () => import('@/components/animate.vue')
const pubu = () => import('@/components/pubu.vue')
const UiLogin = () => import('@/components/UiLogin.vue')
const moveBanner = () => import('@/components/moveBanner/moveBanner.vue')

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'mainPage1',
      redirect: '/mainPage1'
    },
    {
      path: '/mainContain',
      name: 'mainContain',
      component: mainContain
    },
    /* 公共模板 */
    {
      path: '/topNav', // 头部通用
      name: 'topNav',
      component: topNav
    },
    {
      path: '/footer', // 头部通用
      name: 'footer',
      component: footer
    },
    {
      path: '/resInfo', // 头部通用
      name: 'resInfo',
      component: resInfo
    },
    /* 页面模板 */
    {
      path: '/mainPage1',
      name: 'mainPage1',
      component: mainPage1
    },
    {
      path: '/mainPage2',
      name: 'mainPage2',
      component: mainPage2
    },
    {
      path: '/mainPage3',
      name: 'mainPage3',
      component: mainPage3
    },
    {
      path: '/flex',
      name: 'flex',
      component: flex
    },
    {
      path: '/animate',
      name: 'animate',
      component: animate
    },
    {
      path: '/pubu',
      name: 'pubu',
      component: pubu,
    },
    /* 内容详情 */
    {
      path: '/mainPageDetail',
      name: 'mainPageDetail',
      component: mainPageDetail,
    },
    {
      path: '/moveBanner',
      name: 'moveBanner',
      component: moveBanner,
    }
  ]
})



