<template>
  <div id="app">
  	<top-nav v-bind:articalItems="articalItems"></top-nav>
    <keep-alive>
      <router-view></router-view>
    </keep-alive>
    <scroll-top v-show="isGoShow" @goToTop="mainSetGo"></scroll-top>
    <footer-page></footer-page>
    <res-info v-on:child-event="showMessageFromChild" v-show="isChildShow"></res-info>
  </div>
</template>

<script>
  import scrollTop from './components/common/scrollTop.vue';
  import topNav from './components/common/topNav.vue';
  import footer from './components/common/footer.vue';
  import resInfo from './components/common/resInfo.vue';
  import {mapGetters} from 'vuex'
	export default {
		data() {
      return {
          // posts: {
          //       title: this.$store.state.resInfo, // 获取store中info
          // },  
          isChildShow: this.$store.state.isChildShow, // 获取store中info, // total
          isGoShow: false, // 回到顶部,
          articalItems: [
            {"title": "首页", "url": "/mainPage1"},
            {"title": "二叶", "url": "/mainPage2"},
            {"title": "三叶", "url": "/mainPage3"},
            {"title": "弹性布局", "url": "/flex"},
            {"title": "瀑布流布局", "url": "/pubu"},
            {"title": "图片滑动", "url": "/moveBanner"},
          ]
      }
    },
    mounted() {
    },
    methods: {
      /**
      * 关闭黑色弹出框
      */
      showMessageFromChild: function(data) {
        if (!data) {
            this.$store.commit('SET_ISCHILDSHOWS', false); // 该方法比较靠谱
        }
      },
      mainSetGo(data) {
        this.isGoShow = data;
      },
    },
		components: {
		    'top-nav': topNav,
        'scroll-top': scrollTop,
        'footer-page': footer,
        'res-info': resInfo
		}
	}
</script>

<style scoped>
</style>



